import * as config from "./config";

export const pushState = (state, title, url, target) => {
  history.pushState(state, title, url);
  const event = new Event("urlchange", { bubbles: true });
  target.dispatchEvent(event);
};

// https://api.themoviedb.org/3/movie/latest?api_key=<<api_key>>&language=en-US
// https://api.themoviedb.org/3/movie/popular?api_key=de088b40ef657d3aadc907af18e76ae1&language=en-US&page=1#
export const request = async (url, options = {}) => {
  try {
    const fullURL = `${config.THE_MOVIE_DB_URL}${url}?api_key=${config.THE_MOVIE_DB_API_KEY}&language=en-US&page=1#`;
    const response = await fetch(fullURL, options);

    if (!response.ok)
      throw new Error(`ERROR ${response.status}: API Request failed`);

    const json = await response.json();
    return json;
  } catch (e) {
    console.log(e.message);
  }
};
