import Component from "../Component/Component";

export default class Main extends Component {
  constructor(componentInfo) {
    super(componentInfo);

    this.$currentElement = document.createElement("main");
    this.$currentElement.className = "main";

    this.render();
  }

  render() {
    this.$currentElement.innerHTML = "";
    this.$currentElement.textContent = this.$state.activeTab;
    this.$parentElement.appendChild(this.$currentElement);

    console.log(this.$state.activeTab);
  }
}
